@charset "UTF-8";
/********************************************
Estilos generales para colores y fuentes
*********************************************/
:root {
  --principal-color: rgb(255, 255, 255);
  /* Cambia el color principal a blanco */
  --principal-color-a: rgba(255, 255, 255, 0.5);
  /* Cambia el color principal con opacidad a blanco */
  --secondary-color: rgb(222, 35, 51);
  --light-color: rgb(0, 0, 0);
  /* Cambia el color de fondo a negro */
  --bg-color: rgb(244, 246, 247); }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 100%;
  overflow-x: hidden;
  word-wrap: break-word;
  background-color: var(--light-color);
  color: var(--principal-color); }

h2,
h3,
h4 {
  font-weight: 700; }

h2 {
  font-size: 2rem;
  line-height: 1em;
  margin-bottom: 2rem; }

h3 {
  font-size: 1.45rem;
  margin-bottom: 1rem; }

h4 {
  font-size: 1.3rem;
  margin-bottom: 1rem; }

p {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 1rem; }

@media (min-width: 992px) {
  h2 {
    font-size: 2.5rem; }
  h3 {
    font-size: 1.75rem; }
  h4 {
    font-size: 1.4rem; }
  p {
    font-size: 1.2rem; } }

a {
  color: var(--secondary-color);
  text-decoration: none; }

a:hover {
  color: var(--text-color);
  text-decoration: underline; }

/*********************************************
Estilos generales para headers
**********************************************/
header {
  text-align: center; }

h1 {
  font-size: 1.8rem;
  line-height: 1;
  font-weight: 400;
  color: var(--principal-color);
  margin-top: -1rem;
  /* Ajusta el margen superior para acercar el h1 hacia arriba */
  margin-bottom: 0;
  letter-spacing: -.4rem;
  font-family: 'Quattrocento', sans-serif; }

h1 span:first-of-type {
  font-family: 'Quattrocento', sans-serif; }

h1 a {
  color: inherit; }

h1 a:hover {
  color: var(--principal-color-a);
  text-decoration: none; }

@media (min-width: 992px) {
  header {
    text-align: left;
    padding-top: 1rem;
    /* Ajusta el espacio superior del header para reducir la separación entre el subtítulo y el menú de navegación */ }
  h1 {
    font-size: 4.5rem;
    margin-top: -1rem;
    /* Ajusta el margen superior para acercar el h1 hacia arriba */ } }

/*********************************************
Clases para los elementos multimedia del sitio
**********************************************/
.img-fluid {
  max-width: 100%;
  height: auto; }

.video-fluid {
  position: relative;
  width: 100%;
  padding: 0;
  overflow: hidden;
  border: none; }

.video-fluid-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0; }

.video-fluid::before {
  display: block;
  content: "";
  padding-top: 56.25%; }


/*********************************************
Contenedores del sitio
**********************************************/
#portada {
  text-align: center; }

section,
article,
aside,
main {
  margin: 1.5rem 0; }

header {
  padding: 1rem 0 .75rem; }

footer {
  padding: 2.5rem;
  z-index: 98; }

div.espai {
  padding: 5%; }

.separator-line {
  height: 1px;
  background-color: white;
  width: 80%;
  margin: 0 auto;
  /* Centra la línea horizontalmente */
  z-index: 99; }

div.separator-line {
  z-index: 2; }

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem;
  width: 100%; }

@media (min-width: 576px) {
  .container {
    max-width: 540px; } }

@media (min-width: 768px) {
  .container {
    max-width: 720px; } }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1140px; } }

@media (min-width: 1400px) {
  .container {
    max-width: 1320px; } }

/*********************************************
Menú de navegación del sitio
**********************************************/
nav {
  position: sticky;
  top: 0;
  z-index: 1;
  background: var(--light-color);
  padding: 1.4rem 0;
  box-shadow: 0 4px 8px 0 rgba(13, 10, 44, 0.04);
  width: 100%;
  /* Ajusta el ancho al 100% */
  display: flex;
  justify-content: center;
  /* Centra los elementos en el eje horizontal */ }

nav ul {
  text-transform: uppercase;
  font-size: .9rem;
  list-style-type: none;
  display: flex; }

nav li {
  padding: 0 .5rem;
  /* Ajusta el espaciado según sea necesario */
  text-align: center; }

nav li a {
  font-weight: 700;
  color: var(--principal-color); }

nav li a:hover {
  color: var(--principal-color-a);
  text-decoration: none; }

nav li.active a {
  font-weight: 700;
  color: var(--secondary-color); }

nav li.has-submenu.active a {
  font-weight: 700;
  color: var(--secondary-color); }

@media (min-width: 992px) {
  nav ul {
    font-size: 1.2rem; }
  nav ul li {
    margin-right: 30px; }
  nav ul li:last-child {
    margin-right: 0; } }

nav ul {
  text-transform: uppercase;
  font-size: .9rem;
  list-style-type: none;
  display: flex; }

nav li {
  padding: 0 .5rem;
  /* Ajusta el espaciado según sea necesario */
  text-align: center; }

nav li a {
  font-weight: 700;
  color: var(--principal-color);
  text-decoration: none; }

.submenu {
  display: none; }

.has-submenu:hover .submenu {
  display: block; }

/*********************************************
Botón del footer para hacer scroll
**********************************************/
@media (min-width: 768px) {
  #btn-scroll-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #333;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
    opacity: 1;
    /* Muestra el botón de manera predeterminada en versiones móviles */ }
  #btn-scroll-top.show {
    opacity: 1; } }

@media (max-width: 769px) {
  #btn-scroll-top {
    display: none;
    /* Oculta el botón en versiones de escritorio */ } }

/*********************************************
Contenidos de la página de inicio

.hero-image {
    height: 30vh;
    background-color: var(--principal-color);
    background-image: url(../img/bg-hero-home-m.jpg);
    background-size: cover;
    color: var(--light-color);
    display: flex;
    align-items: center;
    text-align: center;
    margin-top: -3rem;
}

.hero-image p {
    font-size: 1.8rem;
    line-height: 1.5;
}

@media (min-width: 768px) {
    .hero-image p {
        font-size: 3rem;
    }

    section.info p {
        font-size: 1.4rem;
    }
}

@media (min-width: 992px) {
    .hero-image {
        background-image: url(../img/bg-hero-home.jpg);
    }

    .hero-image p {
        font-size: 2rem;
        line-height: 1;
    }
}

@media (min-width: 1200px) {
    .hero-image {
        height: 50vh;
    }

    .hero-image p {
        font-size: 6rem;
    }
}

**********************************************/
/*********************************************
Estilos específicos portada
**********************************************/
.contenedor-texto-presentacion {
  text-align: center; }

.contenedor-texto-resumen {
  text-align: center;
  margin: 0% 20%;
  position: relative;
  z-index: 1;
  /* Asegura que el texto esté por encima de la imagen de la mariposa */ }

/*********************************************
Animación mariposa
**********************************************/
body {
  margin: 0;
  /*overflow: hidden;*/ }

#contenedor-texto-mariposa {
  position: relative;
  width: 100%;
  margin: 1px auto 50px;
  /* Ajusta el margen inferior para reducir la separación entre el bloque de la mariposa y el siguiente contenido */
  padding: 13%;
  background-color: #000; }

#mariposa {
  position: absolute;
  width: 70%;
  height: auto;
  top: 1px;
  left: 10%;
  animation: volar 12s linear infinite; }

@keyframes volar {
  0% {
    transform: translateX(0); }
  25% {
    transform: translateX(100px); }
  50% {
    transform: translateX(200px); }
  75% {
    transform: translateX(100px); }
  100% {
    transform: translateX(0); } }

/*********************************************
Animación corazón
**********************************************/
#contenedor-texto-corazon {
  position: relative;
  width: 80%;
  margin: 50px auto;
  padding: 20px;
  background-color: #000; }

#corazon {
  position: absolute;
  width: 30px;
  height: auto;
  top: 50px;
  left: 50px;
  transform-origin: center; }
